button {
    border: none;
    border-radius: 50%;
    height: 2.5rem;
    width: 2.5rem;
}

@media screen and (max-width: 768px) {
    button {
        height: 1.5rem;
        width: 1.5rem;
    }
}

.editButton {
    background-color: var(--yellow);
    background-image: url(../assets/pencil.svg);
    vertical-align: middle;
}

.editButton:hover {
    background-color: var(--midYellow);
}

.saveButton {
    background-color: var(--yellow);
    background-image: url(../assets/save.svg);
    vertical-align: middle;
}

.saveButton:hover {
    background-color: var(--midYellow);
}

.deleteButton {
    background-color: var(--red);
    background-image: url(../assets/minus.svg);
    vertical-align: middle;
}

.deleteButton:hover {
    background-color: var(--midRed);
}

.addButton {
    background-color: var(--green);
    background-image: url(../assets/plus.svg);
    vertical-align: middle;
}

.addButton:hover {
    background-color: var(--midGreen);
}

.tickButton {
    background-color: var(--green);
    background-image: url(../assets/tick.svg);
    vertical-align: middle;
}

.tickButton:hover{
    background-color: var(--midGreen);
}

.crossButton {
    background-color: var(--red);
    background-image: url(../assets/cross.svg);
    vertical-align: middle;
}

.crossButton:hover {
    background-color: var(--midRed);
}

.buttonCentre {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}