@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;500;600;700&display=swap);
:root {
    --lightBlue: #A6E2F0;
    --midBlue: #7DBBCA;
    --darkBlue: #3D405B;
    --green: #81B29A;
    --midGreen: #478466;
    --red: #CC5B3D;
    --midRed: #A03E24;
    --yellow: #F5D118;
    --midYellow: #D4A912;

}
h1 {
    font-family: 'Oswald', sans-serif;
    text-transform: uppercase;
    font-weight: 500;
    font-size: 4rem;
    color: var(--darkBlue);
    margin-top: 3rem;
    margin-bottom: 3rem;
    text-align: center;
}

@media screen and (max-width: 768px) {
    h1 {
        line-height: 4rem;
    }
}

h2 {
    font-family: 'Oswald', sans-serif;
    text-transform: uppercase;
    font-weight: 500;
    font-size: 2rem;
    color: var(--darkBlue);
    margin-top: 0;
    margin-bottom: 1rem;

}

h2.footer {
    background-color: var(--midBlue);
    margin: 0;
    margin-bottom: 1rem;
    /* padding: 2rem; */
    text-align: center;
    /* width: 95%; */
}

/* @media screen and (max-width: 768px) {
    h2.footer {
        width: 80%;
    }
} */

h3 {
    font-family: 'Oswald', sans-serif;
    text-transform: uppercase;
    font-weight: 500;
    font-size:calc(12px + 1.5vw); 
    color: var(--darkBlue);
    padding-bottom: 0.5rem;
    margin: 0;
}

input {
    font-family: 'Oswald', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: var(--darkBlue);
    font-size:calc(12px + 1.5vw); 
}

input::-webkit-input-placeholder {
    color: rgb(175, 175, 175);
    font-style: italic;
}

input:-ms-input-placeholder {
    color: rgb(175, 175, 175);
    font-style: italic;
}

input::-ms-input-placeholder {
    color: rgb(175, 175, 175);
    font-style: italic;
}

input::placeholder {
    color: rgb(175, 175, 175);
    font-style: italic;
}
section {
    margin-left: 2rem;
    margin-right: 2rem;
}

@media screen and (max-width: 768px) {
    section {
        margin-left: 0.5rem;
        margin-right: 0.5rem;
    }
}
button {
    border: none;
    border-radius: 50%;
    height: 2.5rem;
    width: 2.5rem;
}

@media screen and (max-width: 768px) {
    button {
        height: 1.5rem;
        width: 1.5rem;
    }
}

.editButton {
    background-color: var(--yellow);
    background-image: url(/gift-tracker-app/static/media/pencil.f5a83a80.svg);
    vertical-align: middle;
}

.editButton:hover {
    background-color: var(--midYellow);
}

.saveButton {
    background-color: var(--yellow);
    background-image: url(/gift-tracker-app/static/media/save.1ea81968.svg);
    vertical-align: middle;
}

.saveButton:hover {
    background-color: var(--midYellow);
}

.deleteButton {
    background-color: var(--red);
    background-image: url(/gift-tracker-app/static/media/minus.3258cc07.svg);
    vertical-align: middle;
}

.deleteButton:hover {
    background-color: var(--midRed);
}

.addButton {
    background-color: var(--green);
    background-image: url(/gift-tracker-app/static/media/plus.adbb1d22.svg);
    vertical-align: middle;
}

.addButton:hover {
    background-color: var(--midGreen);
}

.tickButton {
    background-color: var(--green);
    background-image: url(/gift-tracker-app/static/media/tick.2e30f8cb.svg);
    vertical-align: middle;
}

.tickButton:hover{
    background-color: var(--midGreen);
}

.crossButton {
    background-color: var(--red);
    background-image: url(/gift-tracker-app/static/media/cross.f17e0544.svg);
    vertical-align: middle;
}

.crossButton:hover {
    background-color: var(--midRed);
}

.buttonCentre {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.table {
    background-color: var(--lightBlue);
    /* padding: 2rem; */
    /* width: 100%; */
}

@media screen and (max-width: 768px) {
    .table {
        padding: 1rem;
        /* width: 80%; */
    }
}

td {
    height: 6rem;
}

td.tableInputs {
    background-color: white;
    border-top: 2px solid var(--lightBlue);
    border-bottom: 2px solid var(--lightBlue);
    text-align: center;
    vertical-align: middle;
    height: 3rem;
    width: 30%;
}

td.tableInputs:last-of-type {
    border-top: 2px solid var(--lightBlue);
    border-bottom: 2px solid var(--lightBlue);
}

.tableTextInput {
    background-color: white;
    border: none;
    text-align: center;
    height: 4rem;
    width: 100%;
    display: block;
    padding: 0;
    margin: 0;
}

.tdNarrow {
    max-width: 4rem;
    padding-right: 1rem;
}

.tableButtons {
    display: block;
    margin: auto;
}

.tableRow {
    border-left: 2rem solid var(--lightBlue);
    border-right: 2rem solid var(--lightBlue);
    width: 100%;
}

.tableRow:last-of-type {
    border-bottom: 2rem solid var(--lightBlue);
}

th {
    background-color: var(--lightBlue);
}

table {
    background-color: var(--lightBlue);
    border-spacing: 0;
}



.cardHolder {
    /* width: 100%; */
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    justify-content: space-between;

}

@media screen and (max-width: 768px) {
    .cardHolder {
        flex-direction: column;
        /* width: 100%; */
    }
}

.card {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 35%;
}

@media screen and (max-width: 768px) {
    .card {
        width: 100%;
        justify-content: center;
        align-items: center;
        
    }
}

.budgetCard {
    background-color: var(--green);
}

.friendCard {
    background-color: var(--yellow);
}
body {
  font-family: 'Oswald', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: var(--darkBlue);
  font-size:calc(12px + 1.5vw); 
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

